import { useState } from 'react';
import {Link} from 'react-router-dom';
// import $ from 'jquery';

const Sidebar = (props) =>{

    let url_array = window.location.href.split('/');

    let url_val = url_array[url_array.length - 1];

    const[url] = useState(url_val);

	return (
		<>	
	        <div className="col-sm-3 col-xs-6 sidebar pl-0">
                <div className="inner-sidebar mr-3">
                    <div className="sidebar-menu-container">
                        <ul className="sidebar-menu mb-4">
                            <li className="parent">
                                <Link to={'/dashboard'}  className={(url==='dashboard') ? "active":""}>
                                    <i className="fa fa-dashboard mr-3"> </i>
                                    <span className="none">Dashboard </span>
                                </Link>
                            </li>
                            <li className="parent">
                                <Link to={'/package'}  className={(url==='package') ? "active":""}>
                                    <i className="fa fa-square-o mr-3"> </i>
                                    <span className="none">Requested Items </span>
                                </Link>
                            </li>
                            {/* <li className="parent">
                                <Link to={{}} onClick={()=>toggle_menu('package')} className=""><i className="fa fa-pencil-square mr-3"> </i>
                                    <span className="none">Package <i className="fa fa-angle-down pull-right align-bottom"></i></span>
                                </Link>
                                <ul className="children" id="package">
                                    <li className="child">
                                        <Link to={'/package'} className="pl-5">
                                            <i className="fa fa-angle-right mr-2"></i>
                                            <span className="none">List</span>
                                        </Link>
                                    </li>
                                </ul>
                            </li> */}
                        </ul>
                    </div>
                </div>
            </div>
		</>
	);
}

export default Sidebar;