const Createprogram = (props)=>{
    
  return(
    <>
        {
            props.formValues.map((element, index) => (
            <div className="mb-3 border border-5 bg-white rounded mr-3 ml-3" key={index}>
                <div className="row mr-1 ml-1">
                <div className="col-sm-6">
                    <label id="program_nameLabel">Name of Program</label>
                    <div className="input-group mb-3">
                        <input 
                            type="text"
                            className="form-control"
                            placeholder="Enter the program name"
                            readOnly={(element["is_new"] === undefined)?  true : false}
                            name="program_name" 
                            onChange={e => props.handleChange(index, e)} 
                            value={element.program_name || ""} 
                            required
                        />
                    </div>
                    <div className="error" id="program_nameError" />
                </div>
                <div className="col-sm-6">
                    <label id="program_runLabel">Run</label>
                    <div className="input-group mb-3">
                        <select
                            name="program_run"
                            value={element.program_run || ""} 
                            onChange={e => props.handleChange(index, e)}
                            required 
                            disabled={(element["is_new"] === undefined)?  true : false}
                            className="browser-default custom-select">
                                <option value='' disabled="disabled">---Select--- </option>
                                <option value="hidden">Hidden</option>
                                <option value="normal">Normal</option>
                        </select>  
                    </div>
                    <div className="error" id="program_runError" />
                </div>
                <div className="col-sm-6">
                    <label id="program_can_runLabel">Program can Run</label>
                    <div className="input-group mb-3">
                        <select
                            name="program_can_run"
                            value={element.program_can_run || ""} 
                            onChange={e => props.handleChange(index, e)}
                            required 
                            disabled={(element["is_new"] === undefined)?  true : false}
                            className="browser-default custom-select">
                                <option value='' disabled="disabled">---Select--- </option>
                                <option value="1">Whether user is logged on or not </option>
                                <option value="2">only user is logged on </option>
                                <option value="3">only when no user is logged on</option>
                        </select>  
                    </div>
                    <div className="error" id="program_can_runError" />
                </div>
                <div className="col-sm-6">
                    <label id="run_modeLabel">Run Mode</label>
                    <div className="input-group mb-3">
                        <select
                            name="run_mode"
                            value={element.run_mode || ""} 
                            onChange={e => props.handleChange(index, e)}
                            required 
                            disabled={(element["is_new"] === undefined)?  true : false}
                            className="browser-default custom-select">
                                <option value='' disabled="disabled">---Select--- </option>
                                <option value="admin">Admin</option>
                                {
                                    (props.formValues[index].program_can_run ==="2") &&
                                    <> <option value="user">User</option> </>
                                }

                        </select>  
                    </div>
                    <div className="error" id="run_modeError" />
                </div>
                {
                (props.formValues[index].program_can_run ==="1" || props.formValues[index].program_can_run ==="2") ?
                    <div className="col-sm-6">
                        <label id="user_to_interactLabel">User to Interact</label>
                        <div className="input-group mb-3">
                            <select
                                name="user_to_interact"
                                value={element.user_to_interact || ""} 
                                onChange={e => props.handleChange(index, e)}
                                required 
                                disabled={(element["is_new"] === undefined)?  true : false}
                                className="browser-default custom-select">
                                    <option value='' disabled="disabled">---Select--- </option>
                                    <option value="Yes">Yes</option>
                                    <option value="No">No</option>
                            </select>
                        </div>
                        <div className="error" id="user_to_interactError" />
                    </div>
                    :
                    null
                }
                <div className="col-sm-6">
                    <label id="command_line_programLabel">Command Line</label>
                    <div className="input-group mb-3">
                        <textarea 
                            className="form-control"
                            name="command_line_program"
                            value={element.command_line_program || ""}
                            placeholder="Enter the text for command line"
                            onChange={e => props.handleChange(index, e)}
                            required
                        />
                    </div>
                    <div className="error" id="command_line_programError" />
                </div>
            </div>
            <div className="row mr-1 ml-1">
                {
                    index ?         
                        <div className="col-sm-6"><button type="button"  className="custom-btn delete mt-3 mb-3" onClick={() => props.removeFormFields(index)}>Delete Program</button></div>
                    : null
                }
            </div>
            </div>
          ))
        }
        {/* {
            (props.counter < 5) ?
            <div className="button-section">
                <button className="button add btn btn-success ml-3 mt-3 mb-3" type="button" onClick={() => props.addFormFields(props.counter)}><i className="fa fa-plus"></i></button>
            </div>
            :null
        } */}
    </>
  )
}
export default Createprogram;