import Img from '../assets/img/profile.jpg';
import Logo from '../assets/img/logo.png';
import {Link, Outlet,useNavigate} from 'react-router-dom';
//import $ from 'jquery';
import '../assets/css/style.css';
import '../assets/css/fontawesome.css';
import Sidebar from './Sidebar';

import AuthService from "../services/Auth.service"; 
const Navbar = () =>{

	// let  {id:userid}  =  AuthService.getCurrentUser();
	let userid = '62ac617485e6c7b4a4087dfb';
	const navigate = useNavigate();

	// const toggle_sidebar = (e)=>{
		
	// 	$('#sidebar-toggle-btn').toggleClass('slide-in');
	//     $('.sidebar').toggleClass('shrink-sidebar');
	//     $('.header-logo').toggleClass('shrink-sidebar');
	//     $('.content').toggleClass('expand-content');
	//     $('.header-menu').toggleClass('expand-content');
	    
	// }
	
	const hangleLogout = (e) =>{
		e.preventDefault();
		AuthService.microsoftLogout();
		navigate('/');
	}
	return (
		<>	
			<div className="container-fluid">
				<div className="row header shadow-sm">
					<div className="col-sm-3 pl-0 text-center header-logo">
					<div className="bg-theme mr-3 pt-3 pb-2 mb-0">
							<h3 className="logo"><Link to='/' className="logo"><img src={Logo} alt="McDonalds Logo" width="32" height="28"/></Link></h3>
					</div>
					</div>
					
					<div className="col-sm-9 header-menu pt-2 pb-0">
						<div className="row mt-1">
							<div className="col-sm-4 col-8 pl-0">
								{/* <span className="menu-icon" onClick={toggle_sidebar}>
									<span id="sidebar-toggle-btn"></span>
								</span> */}
							</div>
							
							<div className="col-sm-8 col-4">
								<div className="row">
									<div className="col-sm-4 col-8 text-right mt-2 portal_name">RSM Portal</div>
									<div className="col-sm-8 col-4 text-right flex-header-menu justify-content-end">
										<div className="mr-2">
											<img src={Img} alt="Adam" className="rounded-circle" width="38px" height="38px"/>
										</div>
										<div className="mr-4">
											<button className="logout" onClick={hangleLogout}>Logout</button>
										</div>
									</div>
								</div>
							</div> 
						</div>    
					</div>
				</div>
			</div>
			<div className="container-fluid">
        <div className="row content-section">
          <Sidebar user={userid}/>
					<Outlet />
				</div>
			</div>
		</>
	);
}

export default Navbar;