import ReactPaginate from "react-paginate";
import { useEffect, useState } from "react";
import {Link} from 'react-router-dom';
import PackageService from "../services/Package.service";
import Loader from "./Loader";
const Listpackage = ()=>{

  const [items, setItems] = useState([]);
  const [query, setQuery] = useState("");
  const [pageCount, setpageCount] = useState(0);
  const [loaderStatus,setLoaderStatus] = useState(false);

  let limit = 10;

  // const getItems = async (page = 1) => {
  //   const res = await fetch(
  //      `https://reqres.in/api/users?page=${page}&per_page=${limit}&query=${query}`
  //   );
  //   const data = await res.json();
  //   const total = data.total;
  //   setpageCount(Math.ceil(total / limit));
  //   // console.log(Math.ceil(total/12));
  //   console.log(data);
  //   setItems(data.data);
  // };

  const getItems = async (page = 1) => {
    // const res = await fetch(
    //    `https://reqres.in/api/users?page=${page}&per_page=${limit}&query=${query}`
    // );
    // const data = await res.json();
    const data = await PackageService.listpackage();
    console.log(data);
    const total = data.length;
    setpageCount(Math.ceil(total / limit));
    // console.log(Math.ceil(total/12));
    setItems(data);
    setLoaderStatus(true);
  };

  useEffect(() => {
    getItems();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handlePageClick = async(data) => {
    console.log(data.selected);
    getItems(data.selected + 1);
    // scroll to the top
    //window.scrollTo(0, 0)
  };

  const handleSearch = async(event)=>{
    event.preventDefault();
    getItems();
  }

  return(
    <>
      <div className="col-sm-9 col-xs-12 content pt-3 pl-0">
        <h5 className="mb-0" ><strong>List Package</strong></h5>
        <div className="row mt-3">
          <div className="col-sm-12">
            <div className="mt-1 mb-3 p-3 button-container bg-white border shadow-sm">
              <div className="mb-1">
                <form onSubmit={handleSearch}>
                  Search Here: <input type="text" name="search_text" autoComplete="off" onChange={(e)=>setQuery(e.target.value)}/>
                  <button type="button" className="btn btn-dark btn-sm mb-1 ml-3" onClick={handleSearch}>Search</button>
                </form>
              </div>
              <div className="table-responsive">
                <table id="example" className="table table-striped table-bordered">
                  <thead>
                    <tr>
                        <th>Id</th>
                        <th>Unique Id</th>
                        <th>Scope Availability</th>
                        <th>Package Name</th>
                        <th>Source Location</th>
                        <th>Command Line</th>
                    </tr>
                  </thead>
                  <tbody>
                  {items.map((item) => {
                    return (
                    <tr key={item.id}>
                        <td>
                          <Link to={'/package/'+item.id}>{item.id}</Link>
                        </td>
                        <td>{item.unique_id}</td>
                        <td>{item.formData.scope_availability}</td>
                        <td>{item.formData.package_name}</td>
                        <td>{item.formData.source_location}</td>
                        <td>{item.formData.command_line}</td>
                    </tr>
                    );
                  })}
                  </tbody>
                  <tfoot>
                    <tr>
                        <th>Id</th>
                        <th>Unique Id</th>
                        <th>Scope Availability</th>
                        <th>Package Name</th>
                        <th>Source Location</th>
                        <th>Command Line</th>
                    </tr>
                  </tfoot>
                </table>
              </div>
            <ReactPaginate
                previousLabel={"Previous"}
                nextLabel={"Next"}
                breakLabel={"..."}
                pageCount={pageCount}
                marginPagesDisplayed={2}
                pageRangeDisplayed={3}
                onPageChange={handlePageClick}
                containerClassName={"pagination justify-content-center"}
                pageClassName={"page-item"}
                pageLinkClassName={"page-link"}
                previousClassName={"page-item"}
                previousLinkClassName={"page-link"}
                nextClassName={"page-item"}
                nextLinkClassName={"page-link"}
                breakClassName={"page-item"}
                breakLinkClassName={"page-link"}
                activeClassName={"active"}
            />
            </div>
          </div>
        </div>
        {
          !loaderStatus
          ?
          <Loader/>
          :
          null
        }
      </div>
    </>
  )
}

export default Listpackage;