import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import PackageService from "../../services/Package.service";
const Singlepackage = () =>{
  let { id } = useParams();
  const [packageData,setPackage] = useState('');
  const [programData,setProgram] = useState([]);
  const [osData,setOS] = useState('');
  useEffect(()=>{
    async function getData(id){
      const packagedata = await PackageService.getSinglepackage(id);
      if(packagedata.formData.operating_system.length > 0){
        let packArray = [] 
        packagedata.formData.operating_system.forEach(element => {
            packArray.push(element.id)
        });
        setOS(packArray.toString());
      }
      setProgram(packagedata.formData.program);
      delete packagedata.formData.program;
      delete packagedata.formData.operating_system;
      setPackage(packagedata.formData);

    }
    getData(id);
  },[id]);
  return(
    <>

            <div className="col-sm-9 col-xs-12 content pt-3 pl-0">
                {/* <h5 className="mb-0" ><strong>Single Package</strong></h5> */}
                <span className="text-secondary">Home <i className="fa fa-angle-right"></i> Package <i className="fa fa-angle-right"></i> 1 </span>
                
                <div className="row mt-3">
                    <div className='col-sm-12'>
                      <table className="table table-striped table-bordered bg-white">
                        <thead>
                          <tr>
                            <th colSpan={2}>Package Detail</th>
                          </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <th scope="row">Country Code</th>
                                <td>{packageData.country_code}</td>
                            </tr>
                            <tr>
                                <th scope="row">Package Name</th>
                                <td>{packageData.package_name}</td>
                            </tr>
                            <tr>
                                <th scope="row">Date</th>
                                <td>{packageData.date_required}</td>
                            </tr>
                            <tr>
                                <th scope="row">Scope Availability</th>
                                <td>{packageData.scope_availability}</td>
                            </tr>
                            <tr>
                                <th scope="row">Software Source Location</th>
                                <td>{packageData.source_location}</td>
                            </tr>
                            <tr>
                                <th scope="row">Command Line</th>
                                <td>{packageData.command_line}</td>
                            </tr>
                            <tr>
                                <th scope="row">SCCM Functionality Description</th>
                                <td>{packageData.sccm_functionality}</td>
                            </tr>
                            <tr>
                                <th scope="row">Operating System</th>
                                <td>{osData}</td>
                            </tr>
                            <tr>
                                <th scope="row">Implementation Start Date</th>
                                <td>{packageData.implementation_start_date}</td>
                            </tr>
                        </tbody>
                      </table>
                      { 
                        programData.map((pack,key)=>
                          <table key={key} className="table table-striped table-bordered bg-white">
                            <thead>
                              <tr>
                                <td colSpan={2}>Programs - {key + 1}</td>
                              </tr>
                            </thead>
                            <tbody>
                                  <tr>
                                      <th scope="row">Program Name</th>
                                      <td>{pack.program_name}</td>
                                  </tr>
                                  <tr>
                                      <th scope="row">Program Run</th>
                                      <td>{pack.program_run}</td>
                                  </tr>
                                  <tr>
                                      <th scope="row">Program Can Run</th>
                                      <td>{pack.program_can_run}</td>
                                  </tr>
                                  <tr>
                                      <th scope="row">Run Mode</th>
                                      <td>{pack.run_mode}</td>
                                  </tr>
                                  <tr>
                                      <th scope="row">User To Interact</th>
                                      <td>{pack.user_to_interact}</td>
                                  </tr>
                                  <tr>
                                      <th scope="row">Command Line Program</th>
                                      <td>{pack.command_line_program}</td>
                                  </tr>
                            </tbody>
                          </table>
                        )
                      }
                    </div>
                </div>
            </div>
    </>
  )
}
export default Singlepackage;