import osdata from '../../data/os.json';
import {Multiselect} from 'multiselect-react-dropdown';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useState,useEffect } from 'react';
import PackageService from '../../services/Package.service';

const Createpackage = (props)=>{

    const [getAllPackages,setAllPackages] = useState([]);
    const [startDate, setStartDate] = useState(new Date());

    const subDays = (date = new Date(),numOfDays) => {
        date.setDate(date.getDate() - numOfDays);
        return date;
    }

    const addDays = (date = new Date(),numOfDays) => {
        date.setDate(date.getDate() + numOfDays);
        return date;
    }

    const newDate = date =>{
        setStartDate(date);
        date = new Date(date);
        var day = ('0' + date.getDate()).slice(-2);
        var month = ('0' + (date.getMonth() + 1)).slice(-2);
        var year = date.getFullYear();
        var hours = date.getHours();
        var minutes = date.getMinutes();

        var impDate = month + '/' + day + '/' + year + ' ' + hours + ':' + minutes;
        props.formInputs.implementation_start_date = impDate;
    }

    const removeData = (event,param)=>{
        props.formInputs[param] = event;
    }
      
    const selectData = (event,param) =>{
        props.formInputs[param] = event;
    }

    useEffect(()=>{
        var selectElement = document.getElementById("multiselectContainerReact");
        selectElement.parentElement.setAttribute("class", "multiselec-parent");
        
        if(props.request_type &&  props.request_type === 'modify'){
            
            async function getPackageData(){
                const packagedata = await PackageService.listpackage();
                // props.counter=packagedata.formData.programs.length;
                setAllPackages(packagedata);
                return packagedata;
            }
            getPackageData();
        }
    },[props.request_type]);

    const handleDate =(item,e)=>{
        var  date = new Date(item.value);
        var day = ('0' + date.getDate()).slice(-2);
        var month = ('0' + (date.getMonth() + 1)).slice(-2);
        var year = date.getFullYear();

        e.target.name = item.name;
        e.target.value = month + '/' + day + '/' + year;

        props.changeHandler(e);
    }

    return (
    <>
        <div className="row">
            <div className="col-sm-6">
                <label id="country_codeLabel">Country Code</label>
                <div className="input-group mb-3">
                    <select
                        name="country_code"  
                        value={props.formInputs.country_code || ""} 
                        onChange={props.changeHandler}
                        required 
                        className="browser-default custom-select">
                            <option value='' disabled="disabled">---Select--- </option>
                            <option value="IN">India</option>
                            <option value="UK">Uniter Kingdom</option>
                            <option value="USA">United States</option>
                    </select>                    
                </div>
                <div className="error" id="country_codeError" />
            </div>
            <div className="col-sm-6">
                <label id="package_nameLabel" className="tip">Package Name</label>
                <i className="fa fa-info-circle tip ml-1" aria-hidden="true"></i>
                <span className="tiptext">Package Name</span>
                <div className="input-group mb-3">
                {
                    (props.request_type && props.request_type === 'modify') ?
                    <select
                        name="package_name"
                        value={props.formInputs.package_name || ""}
                        onChange={props.changeHandler}
                        required 
                        className="browser-default custom-select">
                            <option value='' disabled="disabled">---Select--- </option>
                            {
                                getAllPackages.map((pack,key)=>
                                    <option value={pack.id} key={key}>{pack.formData.package_name}</option>    
                                )
                            }
                    </select>
                    :
                    <input type="text"
                        className="form-control"
                        name="package_name"
                        value={props.formInputs.package_name || ""}
                        placeholder="Enter the package name"
                        onChange={props.changeHandler}
                        required
                    />
                }
                </div>
                <div className="error" id="package_nameError" />
            </div>
            <div className="col-sm-6">
                <label id="date_requiredLabel">Date</label>
                <div className="input-group mb-3">
                    <DatePicker className="form-control" 
                        name="date_required" 
                        selected={props.formInputs.date_required ? new Date(props.formInputs.date_required) : ""} 
                        onChange={(date,e) =>handleDate({ name: "date_required", value: date },e)} 
                        dateFormat="MM-dd-yyyy"
                        placeholderText={'mm-dd-yyyy'}
                        fixedHeight 
                        required
                    />
                </div>
                <div className="error" id="date_requiredError" />
            </div>
            <div className="col-sm-6">
                <label id="scope_availabilityLabel">Scope Availability</label>
                <div className="input-group mb-3">
                    <select
                        name="scope_availability"  
                        value={props.formInputs.scope_availability || ""} 
                        onChange={props.changeHandler}
                        required 
                        className="browser-default custom-select">
                            <option value='' disabled="disabled">---Select--- </option>
                            <option value="market">Market</option>
                            <option value="global">Global</option>
                    </select>
                </div>
                <div className="error" id="scope_availabilityError" />
            </div>
            <div className="col-sm-6">
                <label id="source_locationLabel">Software Source Location</label>
                <div className="input-group mb-3">
                    <input type="text"
                        className="form-control"
                        name="source_location"
                        value={props.formInputs.source_location || ""}
                        placeholder="Enter the text for software source location"
                        onChange={props.changeHandler}
                        required
                    />
                </div>
                <div className="error" id="source_locationError" />
            </div>
            <div className="col-sm-6">
                <label id="command_lineLabel">Command Line</label>
                <div className="input-group mb-3">
                    <textarea 
                        className="form-control"
                        name="command_line"
                        value={props.formInputs.command_line || ""}
                        placeholder="Enter the text for command line"
                        onChange={props.changeHandler}
                        required
                    />
                </div>
                <div className="error" id="command_lineError" />
            </div>
            <div className="col-sm-6">
                <label id="sccm_functionalityLabel">SCCM Functionality Description</label>
                    <div className="input-group mb-3">
                    <textarea 
                        className="form-control"
                        name="sccm_functionality"
                        value={props.formInputs.sccm_functionality || ""}
                        placeholder="Enter The SCCM Functionality Description"
                        onChange={props.changeHandler}
                        required
                    />
                    </div>
                <div className="error" id="sccm_functionalityError" />
            </div>
            <div className="col-sm-6">
                <label id="operating_systemLabel">Operating System</label>
                <div className="input-group mb-3">
                    <Multiselect
                        className="browser-default custom-select"
                        options={osdata}
                        selectedValues={props.formInputs.operating_system}
                        displayValue="id"
                        // showCheckbox={true}
                        name="operating_system"
                        onSelect={(e)=>selectData(e,'operating_system')}
                        onRemove={(e)=>removeData(e,'operating_system')}
                        style={ {searchBox: { border: "none",padding: "7px 5px 0px 8px"},multiselectContainer:{height:"100%",padding: "0px"} }}
                        required
                    />
                    {/* <select
                    name="operating_system"  
                    value={props.formInputs.operating_system || ""} 
                    onChange={props.changeHandler}
                    required 
                    className="browser-default custom-select">
                        <option value='' disabled="disabled">---Select--- </option>
                        {
                            osdata.map((d,key)=>
                                <option key={key} value={d} >{d}</option>
                            )
                        }
                    </select> */}
                </div>
                <div className="error" id="operating_systemError" />

            </div>
            <div className="col-sm-6">
                <label id="business_impactLabel">Business Impact</label>
                    <div className="input-group mb-3">
                    <textarea 
                        className="form-control"
                        name="business_impact"
                        value={props.formInputs.business_impact || ""}
                        placeholder="Enter The Business Impact"
                        onChange={props.changeHandler}
                        required
                    />
                    </div>
                <div className="error" id="business_impactError" />
            </div>
            <div className="col-sm-6">
                <label id="implementation_start_dateLabel">Implementation Start Date</label>
                <div className="input-group mb-3">
                    <DatePicker className="form-control" 
                        name="implementation_start_date" 
                        selected={startDate}
                        onChange = {(date) => newDate(date)}
                        minDate={addDays(new Date(), 10)}
                        // includeDateIntervals={[
                        //     { start: subDays(new Date(), 1), end: addDays(new Date(), 10) },
                        // ]}
                        showTimeSelect 
                        dateFormat="MM-dd-yyyy h:mm aa"
                        placeholderText={'mm-dd-yyyy'}
                        fixedHeight 
                        required
                    />
                </div>
                <div className="error" id="implementation_start_dateError" />
            </div>
        </div>
    </>
  )
}

export default Createpackage;