// import axios from "axios";

import {global,Encrypt,Decrypt} from '../config/global';


const login = (username, password) => {  
  return fetch(global.API_HOST+"/users/login",{
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        email:username,
        password:password,
      })
  })
  .then((response) => {
    return response.json();
  }).then((data)=>{
    if (data.email) {
      localStorage.setItem("user", JSON.stringify(data));
      localStorage.setItem("token", Encrypt(data));
    }
    return data;
  });
};

const logout = () => {
  localStorage.removeItem("user");
  localStorage.removeItem("token");
  // return axios.post(global.API_HOST + "signout").then((response) => {
  //   return response.data;
  // });
};

const getCurrentUser = () => {
  const tokenData  =localStorage.getItem("token");
  if(tokenData){
    // console.log('authservice')
    // console.log(Decrypt(tokenData)); 
    // console.log(JSON.parse(Decrypt(tokenData)).email); 
    // console.log('authservice')
  }
  return JSON.parse(localStorage.getItem("user"));
};

const microsoftLogin = (data)=>{
  const userData = {
    uniqueId: data.uniqueId,
    tokenType: data.tokenType,
    tenantId:data.tenantId,
    name:data.account.name,
    userName:data.account.userName
  };

  // console.log(data);
  console.log(userData);
  localStorage.setItem("user", JSON.stringify(userData));
  localStorage.setItem("token", data.accessToken);
}
const microsoftLogout = ()=>{
  localStorage.removeItem("user");
  localStorage.removeItem("token");
  sessionStorage.clear();
}
const AuthService = {
  login,
  logout,
  getCurrentUser,
  microsoftLogin,
  microsoftLogout
}

export default AuthService;