
import {global} from '../config/global';

const createpackage = (data) =>{
  return fetch(global.API_HOST_LIVE+"/HttpTriggerRSMRequest",{
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data)
  }).then((response)=>{
    return response.json();
  })
  .then((data)=>{
    return data;
  })
}

const listpackage = (limit,offset) =>{
  return fetch(`http://localhost:3000/packages`).then((response)=>{
    return response.json();
  })
  .then((data)=>{
    return data;
  })
}

const getSinglepackage = (id) =>{
  return fetch(`http://localhost:3000/packages/`+id).then((response)=>{
    return response.json();
  })
  .then((data)=>{
    return data;
  })
}

const updatepackage = (data) =>{
  return fetch(`http://localhost:3000/packages/1`,{
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data)
  }).then((response)=>{
    return response.json();
  })
  .then((data)=>{
    return data;
  })
}

const PackageService = {
  createpackage,
  listpackage,
  getSinglepackage,
  updatepackage
}

export default PackageService;