import { BrowserRouter as Router,  Routes,  Route,Navigate} from 'react-router-dom';
import { createBrowserHistory } from 'history';

import AuthService from "../services/Auth.service"; 

import Dashboard from './Dashboard';
import Login from './Login';
import Navbar from './Navbar';
import Listpackage from './Listpackage';
import Singlepackage from './package/Singlepackage';


function PublicRoute({children}){
  const user = AuthService.getCurrentUser();
  var auth = false;
  var url = '';
  if(user){
    auth = true;
    url = "/dashboard";
  }

  return auth ? <Navigate to={url} /> : children;
}

function PrivateRoute({ children }) {
  const user = AuthService.getCurrentUser();
  
  var auth = false;
  if(user){
    auth = true;
  }

  return auth ? children : <Navigate to="/" />;
}

// function PrivateOutlet() {
//   const auth = true;
//   return auth ? <Outlet /> : <Navigate to="/" />;
// }

const Myroutes = () =>{

  const history = createBrowserHistory();

  return (
    <Router history={history}> 
      <Routes>
        {/* Public Route */}
        {/* <Route exact path='/' element={<PublicRoute><Login/></PublicRoute>}></Route> */}
        <Route path="/" element={<PublicRoute><Navbar/></PublicRoute>}>
            <Route index element={<Dashboard />} />
        </Route>
        {/* Private Route */}
        <Route path="/dashboard" element={<PrivateRoute><Navbar/></PrivateRoute>}>
            <Route index element={<Dashboard />} />
        </Route>

        <Route path="/package" element={<PrivateRoute><Navbar/></PrivateRoute>}>
          <Route index element={<Listpackage />} />   
        </Route>
        <Route path="/package/:id" element={<PrivateRoute><Navbar/></PrivateRoute>}>
          <Route index element={<Singlepackage />} />   
        </Route>

        {/* Global Route*/}  
        <Route path="*" element={<Navigate path=""/>}/>
      
      </Routes>
    </Router>
  )
}

export default Myroutes;