const Userinfo = (props) => {
  return(
    <>
      <div className="row">
        <div className="col-sm-6">
            <label id="requester_nameLabel">Requester Name</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="requester_name"
                    readOnly={true}
                    value={props.formInputs.requester_name || ""}
                    placeholder="Requester Name"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="requester_nameError" />
        </div>
        <div className="col-sm-6">
            <label id="email_idLabel">Email ID</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="email_id"
                    disabled={true}
                    value={props.formInputs.email_id || ""}
                    placeholder="Your Email"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="email_idError" />
        </div>
        <div className="col-sm-6">
            <label id="ad_idLabel">AD ID</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="ad_id"
                    readOnly={true}
                    value={props.formInputs.ad_id || ""}
                    placeholder="Your AD ID"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="ad_idError" />
        </div>
        <div className="col-sm-6">
            <label id="locationLabel">Location</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="location"
                    readOnly={true}
                    value={props.formInputs.location || ""}
                    placeholder="Your Location"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="locationError" />
        </div>
        <div className="col-sm-6">
            <label id="user_marketLabel">Market</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="user_market"
                    readOnly={true}
                    value={props.formInputs.user_market || ""}
                    placeholder="Market Name"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="user_marketError" />
        </div>
        <div className="col-sm-6">
            <label id="stateLabel">State</label>
            <div className="input-group mb-3">
                <select
                    name="state"  
                    value={props.formInputs.state || ""} 
                    onChange={props.changeHandler}
                    required 
                    className="browser-default custom-select">
                        <option value='' disabled="disabled">---Select--- </option>
                        <option value="1">New</option>
                        <option value="15">Work in progress</option>
                        <option value="19">Pending Change</option>
                        <option value="12">Pending Problem</option>
                        <option value="22">Pending Tasks</option>
                        <option value="21">Pending Vendor</option>                        
                        <option value="3">Closed Complete</option>
                        <option value="4">Closed Incomplete</option>
                        <option value="14">Closed</option>
                        <option value="18">Awaiting User</option>
                        <option value="23">Pending User Acceptance</option>
                        <option value="24">Fulfilled</option>
                        {/* <option value="1">1 - High</option>
                        <option value="2">2 - Medium</option>
                        <option value="3">3 - Low</option> */}
                </select>
            </div>
            <div className="error" id="stateError" />
        </div>
        <div className="col-sm-6">
            <label id="urgencyLabel">Urgency</label>
            <div className="input-group mb-3">
                <select
                    name="urgency"  
                    value={props.formInputs.urgency || ""} 
                    onChange={props.changeHandler}
                    required 
                    className="browser-default custom-select">
                        <option value='' disabled="disabled">---Select--- </option>
                        <option value="1">1 - High</option>
                        <option value="2">2 - Medium</option>
                        <option value="3">3 - Low</option>
                </select>
            </div>
            <div className="error" id="urgencyError" />
        </div>
        <div className="col-sm-6">
            <label id="impactLabel">Impact</label>
            <div className="input-group mb-3">
                <select
                    name="impact"  
                    value={props.formInputs.impact || ""} 
                    onChange={props.changeHandler}
                    required 
                    className="browser-default custom-select">
                        <option value='' disabled="disabled">---Select--- </option>
                        <option value="1">1 - High</option>
                        <option value="2">2 - Medium</option>
                        <option value="3">3 - Low</option>
                </select>
            </div>
            <div className="error" id="impactError" />
        </div>
        <div className="col-sm-6">
            <label id="group_accessLabel">Group Access</label>
            <div className="input-group mb-3">
                <input type="text"
                    className="form-control"
                    name="group_access"
                    value={props.formInputs.group_access || ""}
                    placeholder="Enter the Group Access"
                    onChange={props.changeHandler}
                    required
                />
            </div>
            <div className="error" id="group_accessError" />
        </div>

        <div className="col-sm-6"></div>
      </div>
    </>
  );
}

export default Userinfo;