import { useState} from "react";
import {useNavigate} from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import AuthService from "../services/Auth.service";
import Createpackage from "./package/Createpackage";
//import Updatepackage from "./package/Updatepackage";
// import Patchpackage from "./package/Patchpackage";
// import Deploypackage from "./package/Deploypackage";
import Loader from "./Loader";
import Userinfo from "./package/Userinfo";
import RequestType from "./package/RequestType";
import Createprogram from "./package/Createprogram";

import PackageService from "../services/Package.service";

window.Buffer = window.Buffer || require("buffer").Buffer;

const Dashboard = () =>{
    
    const navigate = useNavigate();
    const validationNotRequired = [];
    const userdata = AuthService.getCurrentUser();

    const defaultValues = {
        operating_system:[],
        // requester_name:userdata.name,
        // email_id:userdata.userName
        requester_name:'',
        email_id:''
    }
    const [formInputs,setFormInputs]= useState(defaultValues);
    const [formValues, setFormValues] = useState([])
    const [counter,setCounter] = useState(1);
    const [request_type,setRequestType] = useState('create');
    const [show,setShow] = useState(true);
    const [loaderStatus,setLoaderStatus] = useState(false);
    const [showTabs,setShowTabs] = useState('1');
    
    const setShowStatus = ()=>{
        setShow(!show)
        setFormValues([{is_new:''}])
    }

    const changeHandler = async (event) => {
        const name = event.target.name;
        const value = event.target.value;

        if(request_type==="modify" && name==="package_name"){
            setLoaderStatus(true);
            const singlepackage = await PackageService.getSinglepackage(value);

            if(singlepackage.formData.program.length <= 0){
                setShow(true);
            }else{
                setShow(false);
            }
            setFormInputs(singlepackage.formData);
            setFormValues(singlepackage.formData.program)
            setLoaderStatus(false);            
        }
        // if(!validationNotRequired.includes(name)){
        //     event.target.classList.add('active');
        //     event.target.closest('.input-group').classList.remove('mb-3');
            
            
        //     if(value !== ''){
        //         event.target.closest('.input-group').classList.add('mb-3');
        //     }
        //     showInputError(event.target);
        // }
        setFormInputs(values => ({...values, [name]: value}));
    }
    
    const filterProgram = async (formValues) =>{
        formValues.filter(function(b){
            delete b.is_new;
            return b;
        });
          return formValues;
    }

    const handlerSubmit = async (e)=>{
        e.preventDefault();
        console.log(formInputs);
        console.log(showFormErrors());

        if (showFormErrors()) {
            let programValues = await filterProgram(formValues);
            setLoaderStatus(true);
            let formData = {};
            const userData = {
                requester_name:formInputs.requester_name ?? '',
                email_id:formInputs.email_id ?? '',
                ad_id:formInputs.ad_id ?? '',
                location:formInputs.location ?? '',
                user_market:formInputs.user_market ?? '',
                state:formInputs.state ?? '',
                urgency:formInputs.urgency ?? '',
                impact:formInputs.impact ?? '',
                group_access:formInputs.group_access ?? ''
            }

            if(request_type === 'create'){
                formData = {
                    country_code:formInputs.country_code ?? '',
                    package_name:formInputs.package_name ?? '',
                    date_required:formInputs.date_required ?? '',
                    scope_availability:formInputs.scope_availability ?? '', 
                    source_location:formInputs.source_location ?? '', 
                    command_line:formInputs.command_line ?? '', 
                    sccm_functionality:formInputs.sccm_functionality ?? '', 
                    operating_system:formInputs.operating_system ?? '',
                    business_impact:formInputs.business_impact ?? '',
                    implementation_start_date:formInputs.implementation_start_date ?? '',
                    program:programValues
                };
                const dataObject={unique_id:uuidv4(),request_type,userData,formData};
                const createpackageresponse = await PackageService.createpackage(dataObject);
                if(createpackageresponse){
                    setLoaderStatus(false);
                    // navigate("/package");
                }
            }else if(request_type === 'modify'){
                formData = {
                    country_code:formInputs.country_code ?? '',
                    package_name:formInputs.package_name ?? '',
                    date_required:formInputs.date_required ?? '',
                    scope_availability:formInputs.scope_availability ?? '', 
                    source_location:formInputs.source_location ?? '', 
                    command_line:formInputs.command_line ?? '', 
                    sccm_functionality:formInputs.sccm_functionality ?? '', 
                    operating_system:formInputs.operating_system ?? '',
                    business_impact:formInputs.business_impact ?? '',
                    implementation_start_date:formInputs.implementation_start_date ?? '',
                    program:formValues
                };
                const dataObject={unique_id:uuidv4(),request_type,userData,formData};
                const updatepackageresponse = await PackageService.updatepackage(dataObject);
                if(updatepackageresponse){
                    setLoaderStatus(false);
                    navigate("/package");
                }
            }
        }
        
        //     }else if(request_type ==='deploy'){
        //         formData = {
        //             product_description:formInputs.product_description ?? '',
        //             market:formInputs.market ?? '',
        //             enviornment:formInputs.enviornment ?? '',
        //             sccm_package:formInputs.sccm_package ?? '',
        //             package_location:formInputs.package_location ?? '',
        //             command_line:formInputs.command_line ?? ''
        //         }
        //     }else if(request_type ==='update'){
        //         formData = {
        //             market_name:formInputs.market_name ?? '',
        //             enviornment:formInputs.enviornment ?? '',
        //             device_type:formInputs.device_type ?? '',
        //             target_os:formInputs.target_os ?? '',
        //             software_update:formInputs.software_update ?? '',
        //             department_type:formInputs.department_type ?? '',
        //             deployment_batch:formInputs.deployment_batch ?? '',
        //             target_collection:formInputs.target_collection ?? '',
        //             deployment_schedule:formInputs.deployment_schedule ?? '',
        //             department_deadline:formInputs.department_deadline ?? '',
        //             schedule_evaluation:formInputs.schedule_evaluation ?? ''
        //         }
        //     }
            
        //     const dataObject={request_type,userData,formData};
        //     console.log(dataObject);


        //   console.log('Form is valid: do not submit');
        // }
    }
    let addFormFields = () => {
        setCounter(formValues.length + 1);
        setFormValues([...formValues, {is_new:''}])
    }

    let handleChange = (i, e) => {
        let newFormValues = [...formValues];
        newFormValues[i][e.target.name] = e.target.value;
        if(e.target.name === "program_can_run"){
            newFormValues[i]["run_mode"] = '';
        }
        if(e.target.name === "program_can_run" && (e.target.value === "1" || e.target.value === "3")){
            newFormValues[i]["run_mode"] = 'admin';
        }
        setFormValues(newFormValues);
    }

    let removeFormFields = (i) => {
        setCounter(counter - 1);
        let newFormValues = [...formValues];
        newFormValues.splice(i, 1);
        setFormValues(newFormValues)
    }

    const showFormErrors = ()=>{
        var elements = "input,select,textarea";
        const inputs = document.querySelectorAll(elements);
        let isFormValid = true;

        inputs.forEach(input => {
            const name = input.name;
            if(!validationNotRequired.includes(name)){
                input.classList.add('active');
                input.closest('.input-group').classList.add('mb-3');

                const isInputValid = showInputError(input);

                if (!isInputValid) {
                    isFormValid = false;
                    input.closest('.input-group').classList.remove('mb-3');
                }
            }
        });

        return isFormValid;
    }

    const showInputError = (input)=>{
        //const name = input.name;
        const validity = input.validity;
        const label = input.closest('.input-group').previousSibling.textContent;
        const error = input.closest('.input-group').nextSibling;

        if (!validity.valid) {
            if (validity.valueMissing) {
            error.textContent = `${label} is a required field`; 
            }
            return false;
        }
        if(input.name === 'operating_system_input'){
            const searchWrap = document.querySelector('.search-wrapper');
            if(document.querySelector('.search-wrapper > .chip') === null){
                error.textContent = `${label} is a required field`;
                searchWrap.classList.add('invalid');
                return false;
            }else{
                searchWrap.classList.remove('invalid');
                searchWrap.classList.add('valid');
            }
        }

        error.textContent = '';
        return true;
    }

    const handleClick = (value) => {
        if(value === '2'){
            if (showFormErrors()) {
                setShowTabs(value);
            }
        }else{
            setShowTabs(value);
        }
    }

    return (
        <>
            <div className="col-sm-9 col-xs-12 content pt-3 pl-0">
                <form onSubmit={handlerSubmit} autoComplete="off" noValidate>
                    <div className="container d-flex mt-3">
                        <nav aria-label="breadcrumb " className="first d-md-flex" >
                            <ol className="breadcrumb indigo lighten-6 first-1 mb-3  ">
                                <li className="breadcrumb-item "><button className={(showTabs === '1')?"active-1 breadcrum_text mr-2":"active-1 breadcrum_text_user mr-2"}>{(showTabs === '1')?1:<i className="fa fa-check mr-2" ></i>}</button><span className="breadcrum_text mt-1 mr-3">User Information</span><i className="fa fa-angle-right right-arrow mr-2" ></i></li>
                                <li className="breadcrumb-item "><button className={(showTabs === '1')?"active-2 breadcrum_text mr-2":"active-1 breadcrum_text mr-2"}>2</button><span className={(showTabs === '1')?"tech mt-1":"breadcrum_text mt-1"}>Technical Information</span></li>
                            </ol>
                        </nav>
                    </div>
                    {(showTabs === '1') ?
                    <div className="row mt-3">
                        <div className="col-sm-12">
                            <div className="mb-3 p-3 button-container bg-white border shadow-sm">
                                <p className="section_heading">User Information</p>
                                <Userinfo formInputs={formInputs} changeHandler={changeHandler}/>
                                <button className="custom-btn next-btn" onClick={()=>handleClick('2')}>Next: Technical Informtion</button>
                            </div>
                        </div>
                    </div>
                    :null}

                    {(showTabs === '2') ?
                    <div className="row mt-3">
                        <div className="col-sm-12">
                                <div className="mb-3 p-3 button-container bg-white border shadow-sm">
                                <p className="section_heading">Technical Information</p>
                                    <RequestType request_type={request_type} setRequestType={(e)=>setRequestType(e.target.value)}/>
                                    {
                                        (request_type && request_type === 'create')
                                        ?
                                            <>
                                                <Createpackage request_type={request_type} formInputs={formInputs} changeHandler={changeHandler}/>
                                                {
                                                    show 
                                                    ?
                                                        <div className="mb-3 mt-3 card">
                                                            <div className="card-body text-center button-card">
                                                                <p>You can create upto 5 programs</p> 
                                                                <button className="button add btn custom-btn" type="button" onClick={setShowStatus}>Create Program</button>
                                                            </div>
                                                        </div>
                                                    :
                                                    <>
                                                        <div className="mb-3 border border-5 button-card rounded">
                                                            <p className="section_heading mb-2 mt-3 ml-3">Create Program</p>
                                                            <Createprogram request_type={request_type} counter={counter} formValues={formValues} addFormFields = {addFormFields} handleChange={handleChange} removeFormFields={removeFormFields}/>
                                                        </div>
                                                        {(counter < 5) ?
                                                        <div className="mb-3 mt-3 card">
                                                            <div className="card-body text-center button-card">
                                                                <p>You can create upto {5-counter} programs</p> 
                                                                <button className="button add btn custom-btn" type="button" onClick={() => addFormFields(counter)}>Create Program</button>
                                                            </div>
                                                        </div>
                                                        :null}
                                                    </>
                                                }
                                            </>
                                        :
                                        null
                                    }

                                    {
                                        (request_type && request_type === 'modify')
                                        ?
                                            <>
                                                <Createpackage request_type={request_type} formInputs={formInputs} changeHandler={changeHandler}/>
                                                {
                                                    show 
                                                    ?
                                                    <div className="mb-3 mt-3 card">
                                                        <div className="card-body text-center button-card"> 
                                                            <button className="button add btn custom-btn" type="button" onClick={setShowStatus}>Update Program</button>
                                                        </div>
                                                    </div>
                                                    :
                                                    <>
                                                        <div className="mb-3 border border-5 button-card rounded">
                                                            <p className="section_heading mb-2 mt-3 ml-3">Update Program</p>
                                                            <Createprogram request_type={request_type} counter={counter} formValues={formValues} addFormFields = {addFormFields} handleChange={handleChange} removeFormFields={removeFormFields}/>
                                                        </div>
                                                    </>
                                                }
                                            </>
                                        :
                                        null
                                    }

                                    {/* {
                                        (request_type && request_type === 'deploy')
                                        ?
                                            <>
                                                <Deploypackage formInputs={formInputs} changeHandler={changeHandler}/>
                                            </>
                                        :
                                        null
                                    } */}
                                    {/* {
                                        (request_type && request_type === 'patch')
                                        ?
                                            <>
                                                <Patchpackage formInputs={formInputs} changeHandler={changeHandler}/>
                                            </>
                                        :
                                        null
                                    } */}
                                    
                                    {
                                        (request_type && request_type !== '')
                                        ?
                                            <div className="row">
                                                <div className="col-sm-6">
                                                    <div className="input-group mt-3 mb-3">
                                                        <button type="button" name="back" className="logout back-btn mr-2" onClick={()=>handleClick('1')}>Back: User Informtion</button>
                                                        <button name="Submit" className="custom-btn submit-btn">Submit</button>
                                                    </div>
                                                </div>
                                                <div className="col-sm-6"></div>
                                            </div>
                                        :
                                        null
                                    }
                                </div>
                        </div>
                    </div>
                    :
                    null}
                </form>
                {
                    loaderStatus
                    ?
                    <Loader/>
                    :
                    null
                }

            </div>
        </>
    );
}

export default Dashboard;