import CryptoJS from "crypto-js";


export const global = {
    API_HOST:process.env.REACT_APP_BASE_URL,
    API_HOST_LIVE:process.env.REACT_APP_BASE_URL_LIVE
}

export const Encrypt = (value)=>{
    const iv = CryptoJS.lib.WordArray.random(8);
    const key = CryptoJS.lib.WordArray.random(16);

    const fiv = CryptoJS.enc.Utf8.parse(iv);
    const fkey = CryptoJS.enc.Utf8.parse(key);
    const enc = CryptoJS.AES.encrypt( JSON.stringify(value), fkey, {
            iv: fiv,
            mode: CryptoJS.mode.CBC
    });

    var encodedtext = enc.ciphertext.toString(CryptoJS.enc.Base64);    
    
    return iv+key+'.'+encodedtext;
}

export const Decrypt = (value)=>{

    // let encString = value;
    let iv = value.substring(0, 16);
    let key = value.substring(16, 48);
    let text = value.substring(value.indexOf('.') + 1);
    
    const fkey = CryptoJS.enc.Utf8.parse(key);
    const fiv = CryptoJS.enc.Utf8.parse(iv);

    const dec = CryptoJS.AES.decrypt(text, fkey, {
            iv: fiv,
            mode: CryptoJS.mode.CBC
    });

    let decodedText = dec.toString(CryptoJS.enc.Utf8);

    return decodedText;
}