const RequestType = (props)=>{
  return(
    <div className="row">
        <div className="col-sm-6">
            <label id="request_typeLabel">Request Type</label>
            <div className="input-group mb-3">
                <select name="request_type" value={props.request_type} onChange={props.setRequestType} required className="browser-default custom-select">
                    <option value='' disabled="disabled">---Select--- </option>
                    <option value="create"> Create Package </option>
                    <option value="modify"> Modify Package </option>
                    {/* <option value="deploy"> Deploy Package </option>

                    <option value="patch"> Patch Deployment </option> */}
                </select>
            </div>
            <div className="error" id="request_typeError" />
        </div>
        <div className="col-sm-6"></div>
    </div>
  )
}
export default RequestType;